$tamanoBorde:3px;

.educacionContenedor{
    display:flex;
    flex-direction: column;
    color:white;
    padding:1rem 0;
    text-align: center;
    justify-items: center;

    .certificadosContenedor{
        display:flex;
        flex-wrap:wrap;
        margin:0 10%;
        justify-content:space-evenly;

        .certificado{
            width:13.33rem;
            margin: 0.66rem;
            border: transparent solid $tamanoBorde;
        }

        .certificado:hover{
            cursor: pointer;
            border: rgb(0, 225, 255) solid  $tamanoBorde;
        }
    }
}

.certificadoCompletoContenedor{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(27, 27, 27, 0.8);
    backdrop-filter: blur(2px);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
    user-select:none;

    .certificadoZoomContenedor{
        width: 90%;
        height: 100%;
        max-width: 2000px;

        .cerrarContenedor{
            width: 100%;
            height: 3rem;
            display:flex;
            justify-content: right;
            margin:20px 0 0 0;
            #cerrar{
                width: 3rem;        
                opacity: 30%;
            }
        
            #cerrar:hover{
                opacity: 100%;
                cursor: pointer;
            }
        }
    }
}

//Tablet
@media(max-width:768px){
    .certificadoZoomContenedor{
        display: flex;
        flex-direction: column;
        justify-content: center;
   
        #mostrarCarrusel{
            margin: 2rem;
        }
    }
}
