@import 'barraNavegacion';
@import 'encabezado';
@import 'acercaDe';
@import 'proyectos';
@import 'carrusel';
@import 'educacion';
@import 'habilidades';
@import 'contacto';
@import 'footer';
@import 'creditos';
@import 'variables';


body{
    background:$background;
    font-size: $tamanoFuentePrincipal;
}

.subtitulo{
    font-weight: 200;
    font-size: 2rem;
}

.apartado{
    background-color: $apartado;
    margin-top:2rem;
}

.sombra{
    filter:$sombra;
}


//Monitor
@media(min-width:$tamanoMonitor){
    body{
        font-size: $tamanoFuentePrincipal;
        .barraNavegacion{
            max-width: 3000px;
        }
        max-width: 3000px;
    }
}

//Tablet
@media(max-width:$tamanoTablet){
    body{
        font-size: $tamanoFuentePrincipalTablet;
    }
}

//Smartphone
@media(max-width:$tamanoSmartphone){
    body{
        font-size: $tamanoFuentePrincipalSmartphone;
        overflow:scroll;
        .barraNavegacion{
            min-width: $tamanoSmartphone;
            overflow:scroll;
        }
        min-width: $tamanoSmartphone;
    }
}
