.carruselContenedor{
    margin:auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content:center;
    user-select: none;
    width: 100%;
    
    .flechaCarrusel{
        padding:25px;
        opacity:30%;
    }

    .izquierda2{
        display:none
    }

    .descripcion{
        padding-top:20px;
        margin:auto;
        width:75%;
    }

    .flechaCarrusel:hover{
        opacity:100%;
        cursor:pointer;
    }
}

.tipoProyecto{
    font-size: 1rem;
    font-weight: 200;
    user-select: none;
}

@media (min-width:769px){
    .imagenCarrusel{
        width:60%;
        height: 27rem;
        margin:10px;
    }

    #flechaIzquierda{
        order:1;
    }

    #imagen{
        order:2;
    }

    #flechaDerecha{
        order:3;
    }

    #descripcion{
        order:4;
    }
}

@media (max-width:768px){
    .imagenCarrusel{
        width: 100%;
        height: 20rem;
        margin:10px;
    }

    #flechaIzquierda{
        order:2;
    }

    #imagen{
        order:1;
    }

    #flechaDerecha{
        order:3;
    }

    #descripcion{
        order:4;
    }
}

@media(max-width:425px){
    .imagenCarrusel{
        width: 100%;
        height: 15rem;
        margin:10px;
    }
}